$primaryColor: #053742;
$primaryHover: #03232b;
$secondaryColor:#39a2db;
$tertiaryColor:#a2dbfa;
$quaternaryColor:#e8f0f2;



.header{
    height: 50px;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    margin-bottom: 25px;
    background-color: rgba($color: $primaryColor, $alpha: 1.0);
    

    .logo-container{
        height: 100%;
        width: 70px;
        padding: 25px;

    }

    .leftNavItems{
        width: fit-content;
        height: 100%;
        display: flex;

        flex-flow: row nowrap;

        align-items: center;
        justify-content: flex-start;

        
    }

    .rightNavItems{
        width: fit-content;
        height: 100%;
        display: flex;

        flex-flow: row nowrap;

        align-items: center;
        justify-content: flex-end;
        .tool-divider{
            width: 0px;
            border-left: 1px solid rgba($color: $tertiaryColor, $alpha: 0.5);
            margin: 13px 10px 14px;
            height: 30px !important;
        }
    }
    
    .navItem {
        align-items: center;
        font-size: small;
        display: flex;
        flex-flow: row nowrap;
        height: 100%;
        padding: 0 10px;
        cursor: pointer;
        position: relative;

        color: rgba($color: $tertiaryColor, $alpha: 1.0);
        text-decoration: none;
        &:hover{
            background-color: rgba($color: $primaryHover, $alpha: 0.6);

        }
        &:hover .navItemDropdown{
            display: block;
        }
        
    }

    .navItemDropdown{
        position: absolute;
        left: 0;
        top: 100%;
        display: none;
        margin: 0 0;
    }


    

    
}